<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>
          STASH Scanner
        </h1>
        <hr>
      </div>
    </div>
    <div class="row" v-if="!isError">
      <div class="col-xs-12">
        <qrcode-stream @decode="onDecode" @init="onInit" :paused="paused" :track="repaint"></qrcode-stream>
      </div>
    </div>
    <div class="row" v-if="isError">
      <div class="col-xs-12 text-xs-center">
        <br>
        <h3>
          <b>{{ errorString }}</b>
        </h3>
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 text-xs-center">
        <hr>
        <h3>
          Place your code in the center of the camera
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "viewQRCodeReader",

  data () {
    return {
      paused: false,
      isError: false,
      errorString: '',
    }
  },

  methods: {
    onDecode (decodedString) {
      this.paused = true

      this.$swal.fire({
        type: 'info',
        title: 'STASH Scanner',
        text: 'Cound found ' + decodedString,
        showConfirmButton: false,
        showCloseButton: true,
      })
      
      console.log('viewQRCodeReader', decodedString)
    },

    async onInit (promise) {
      // show loading indicator

      try {
        await promise

        // successfully initialized
      } catch (error) {
        this.isError = true;

        if (error.name === 'NotAllowedError') {
          this.errorString = 'Camera not found';
          // user denied camera access permisson

        } else if (error.name === 'NotFoundError') {
          this.errorString = 'Camera not found';
          // no suitable camera device installed

        } else if (error.name === 'NotSupportedError') {
          this.errorString = 'Connection not stable';
          // page is not served over HTTPS (or localhost)

        } else if (error.name === 'NotReadableError') {
          this.errorString = 'Camera not found';
          // maybe camera is already in use

        } else if (error.name === 'OverconstrainedError') {
          this.errorString = 'Camera not found';
          // passed constraints don't match any camera.
          // Did you requested the front camera although there is none?

        } else if (error.name === 'StreamApiNotSupportedError') {
          this.errorString = 'Camera not found';
          // browser seems to be lacking features

        }

        this.errorString = 'Camera not found';
      } finally {
        // hide loading indicator
      }
    },

    repaint (location, ctx) {
      const {
        topLeftCorner,
        topRightCorner,
        bottomLeftCorner,
        bottomRightCorner,
        // topLeftFinderPattern,
        // topRightFinderPattern,
        // bottomLeftFinderPattern
      } = location

      ctx.strokeStyle = '#ffba00' // instead of red

      ctx.beginPath()
      ctx.moveTo(topLeftCorner.x, topLeftCorner.y)
      ctx.lineTo(bottomLeftCorner.x, bottomLeftCorner.y)
      ctx.lineTo(bottomRightCorner.x, bottomRightCorner.y)
      ctx.lineTo(topRightCorner.x, topRightCorner.y)
      ctx.lineTo(topLeftCorner.x, topLeftCorner.y)
      ctx.closePath()

      ctx.stroke()
    }

  }
}
</script>

<style>

</style>



